import React from "react"
import style from './TodoListItems.module.css'



function TodoListItems({title,status,id,handleToggleStatus,isFav,handleToggleFavorite,handleDelete}){

    return(
        <div className ={style.listBox}>
            <input type="checkbox"checked={status} onChange={()=>handleToggleStatus(id)}/>
            <p className ={style.title} style={status?{textDecoration:"line-through",color:"teal"}:null}>{title}</p>
               {!isFav? <img onClick={()=>handleToggleFavorite(id)} style={{color: "yellow"}} src="https://img.icons8.com/ios/452/star--v1.png" alt=""/> : <img onClick={()=>handleToggleFavorite(id)} src="https://png2.cleanpng.com/sh/ab80c05b0c8334cbd0a166b6b773f663/L0KzQYm3UcA4N5J0iZG9NUi6cYPrWPIzamI2SaQ8NES8SIqCVfQ0PmFpftg8Mz24cYa4gsEyPJI1faZtND65RIq3UcE4O2I6Sac8MEO0SIm9VckyOWI5TJD5bne=/4587a2d8b2b111234498995d360dff33-5a51b114a0e4d4.64901173151530318865911144.png" alt=""/>}
               <button onClick = {()=>handleDelete(id)}><img src ="https://icons-for-free.com/iconfiles/png/512/delete+remove+trash+trash+bin+trash+can+icon-1320073117929397588.png" alt=""/></button> 
        </div>
    )
}

export default TodoListItems