import React from"react"
import TodoInput from "./TodoInput"
import {v4 as uuid} from "uuid"
import TodoList from "./TodoList"
import style from './ToDo.module.css'

function ToDo(){
const [tasks,setTasks]=React.useState([])
const [task,setTask] = React.useState("")
const [completedFlag,setCompletedFlag] = React.useState(false)
const handleTaskChange =(e)=>{
    setTask(e.target.value)
}
const handleChange = (title)=>{

    const payload ={
        title,
        status : false,
        id :uuid(),
        isFav : false,
        pinTop : false
    }
    title==""?alert("Add Task...") : setTasks([...tasks,payload])
    setTask("")
}
const handleToggleStatus =(id)=>{
    const newTasks = tasks.map(task=>task.id === id? {...task,status:!task.status}:task)
    setTasks(newTasks)
}
const handleToggleFavorite =(id)=>{
    const newTasks = tasks.map(task=>task.id === id? {...task,isFav:!task.isFav}:task)
    setTasks(newTasks)
}
const handleDelete =(id)=>{
    const newTasks = tasks.filter(task=>task.id !== id)
    setTasks(newTasks)
}
    return(
            <div className={style.mainBox}>
                <div className={style.Head}>
                    <h1>ToDo</h1>
                    <div>
                   <label>Completed</label> <input type="checkbox" checked={completedFlag} onChange ={()=>setCompletedFlag(!completedFlag)}/>
                   </div>
                </div>
                <TodoList tasks ={tasks} handleToggleStatus={handleToggleStatus} handleToggleFavorite={handleToggleFavorite} handleDelete={handleDelete} completedFlag={completedFlag}/>
                <TodoInput handleChange={handleChange} task={task} handleTaskChange={handleTaskChange}/>
            </div>


    )
}

export default ToDo