import React from "react"
import style from './TodoInput.module.css'

function TodoInput({handleChange,handleTaskChange,task}){
        const [inputFlag,setInputFlag] = React.useState(false)

        const handleInputs=()=>{
           setInputFlag(!inputFlag)
           task !== "" &&  handleChange(task)
        }
    return(
        <div className={style.inputBox}>
           
               {inputFlag&& <input type="text" value={task} placeholder="Add Task... " onChange={handleTaskChange}/>  } 
                <button onClick={handleInputs}>+</button>
        </div>
    )
}

export default TodoInput

