import React from "react"
import TodoListItems from "./TodoListItems"
import style from './TodoList.module.css'

function TodoList({tasks,handleToggleStatus,handleToggleFavorite,handleDelete,completedFlag}){
    return (
        <div className={style.mainBox}>
           {tasks.filter(task=>task.isFav && !task.status ).length? <h6>Favorite Tasks</h6>:null}
           { tasks.filter(task=>task.isFav&&!task.status).map(task=> <TodoListItems key={task.id} {...task} handleToggleStatus={handleToggleStatus} handleToggleFavorite={handleToggleFavorite} handleDelete={handleDelete}/>)}

           {tasks.filter(task=>!task.isFav && !task.status ).length? <h6>New Tasks</h6>:null}
           { tasks.filter(task=>!task.isFav&&!task.status).map(task=> <TodoListItems key={task.id} {...task} handleToggleStatus={handleToggleStatus} handleToggleFavorite={handleToggleFavorite} handleDelete={handleDelete}/>)}


           {completedFlag&& tasks.filter(task=>task.status).length ? <h6>Completed tasks</h6>:null}
           {completedFlag&&  tasks.filter(task=>task.status).map(task=> <TodoListItems key={task.id} {...task} handleToggleStatus={handleToggleStatus} handleToggleFavorite={handleToggleFavorite} handleDelete={handleDelete}/>)}
        </div>
    )
}

export default TodoList